@import url(https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: linear-gradient(to left, #B6FBFF, #83A4D4);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/* Vertically centerning all items of `homepage`
   Source: https://stackoverflow.com/a/22196760/9481106 */
.homepage {
    min-height: 100%;  /*may be this line is redundant*/
    min-height: 100vh;

    display: flex;
    align-items: center;
}


/* Sticking footer in bottom
   Source: https://css-tricks.com/couple-takes-sticky-footer/#there-is-calc-reduced-height-wrappers; */
.content-body {
    min-height: calc(100vh - 269px);
}
/* https://stackoverflow.com/questions/51710700/react-datepicker-input-width-will-not-adjust-to-100 */

.react-datepicker__input-container {
  width: inherit;
}

.react-datepicker-wrapper {
  width: 100%;
}


.dnd {
    position: relative;
    width: 100%;
    height: 150px;
}


/*same as form-control:focus except background*/
.dnd-focus {
    color: #495057;
    border-color: #80bdff;
    background: #80bdff;
    /*background-color: #fff;*/
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}


.btn-close {
    border: 0px solid transparent;
}

.icon {
    display: flex;
    align-items: center;
    justify-content: center;
}


.progress {
    height: calc(1.5em + 0.75rem + 2px);
    font-size: 1rem;
}


/*source: https://mdbootstrap.com/plugins/jquery/gallery/#filter*/

.gallery {
  -webkit-column-count: 3;
          column-count: 3;
  -webkit-column-width: 33%;
          column-width: 33%;
}


@media (max-width: 900px) {
  .gallery {
    -webkit-column-count: 2;
            column-count: 2;
    -webkit-column-width: 50%;
            column-width: 50%;
  }
}


@media (max-width: 500px) {
  .gallery {
    -webkit-column-count: 1;
            column-count: 1;
    -webkit-column-width: 100%;
            column-width: 100%;
  }
}


